.header {
    background-color: rgb(21, 43, 61);
    border-radius: 1rem;
    display: flex;

}
.heding {
    padding-left: 4rem;
    padding-top: 0.5rem;
    background-color: rgb(21, 43, 61);
    color: rgb(255, 255, 255);
}


@media screen and (max-width:620px){
 
.heding  {
  font-size: 1.5rem;
  padding-top: 1.2rem;

}


   
}

@media screen and (max-width:500px){
    
.heding {
    padding-left: 2rem;
}

}

@media screen and (max-width:420px){
    
    .heding {
        padding-left: 1rem;
  font-size: 1rem;

    }
    
    }